<template>
  <section class="tidbits-section">
    <h1 class="tidbits-title">獨家花絮</h1>

    <app-swiper>
      <app-swiper-slide :key="v.id.videoId" v-for="v in videos">
        <img :src="v.snippet.thumbnails.medium.url" alt="">
      </app-swiper-slide>
    </app-swiper>
  </section>
</template>

<script>
import {fetchVideos} from '@/api';

export default {
  data() {
    return {
      videoPayload: {},
      selectedVideo: null
    };
  },

  computed: {
    videos() {
      return this.videoPayload.items;
    },
    videoUrl() {
      if (!this.selectedVideo) {
        return '';
      }
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`;
    }
  },

  async mounted() {
    this.videoPayload = await fetchVideos();
  }
};
</script>

<style lang="scss" scoped>

.tidbits-section {
}

.tidbits-title {
  font-size: 2.5rem;
  text-align: center;
}

</style>
